import { HomePage } from './js/home'

// wait for document loaded
window.addEventListener('load', function (event) {
  HomePage()
})


let logoNeon = document.querySelector(".logo__neon");

let delay = 1500;

let timerId = setTimeout(function request() {

  if (logoNeon.classList.contains("neon")) {
    delay = 5000;
    logoNeon.classList.remove("neon")
    document.querySelector(".logo__neon").animate([
      // keyframes
      { opacity: '1' },
      { opacity: '1' }
    ], {
      // timing options
      duration: 0.01,
      iterations: Infinity
    })

  } else {
    delay = 1500;
    logoNeon.classList.add("neon")
    document.querySelector(".neon").animate([
      // keyframes
      { opacity: '1' },
      { opacity: '0.9' }
    ], {
      // timing options
      duration: 0.01,
      iterations: Infinity
    })
  }

  timerId = setTimeout(request, delay);

}, delay);